import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class TextControl implements IControl{
    
    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.Text;
    }

    /**
     * 
     */
    placeholder:string;
    /**
     * 
     */
    isExtend:boolean;
    /**label */
    showLabel:boolean;
    /** */
    required:boolean;
}