export interface CheckboxIEnumValue{
    getEnumValueName():string;
    getEnumValue():any;

    /**
     * 
     */
    value:string;
}

export class CheckboxEnumValue implements CheckboxIEnumValue{

    getEnumValueName():string{
        return this.name;
    }

    getEnumValue():any{
        return this.value;
    }
    
    /**
     * 
     */
    name:string;

    /**
     * 
     */
    value:string;
}