import { IEnumValue } from "./ienumvalue";
import { EnumType } from "./enumtype";

/**
 * Bool
 */
export class EnumBoolValue implements IEnumValue{
    
    getEnumType(): EnumType {
        return  EnumType.BoolType;
    }

    getEnumValueName():string{
        return this.name;
    }

    getEnumValue():any{
        return this.value;
    }

    /**
     * 
     */
    name:string;

    /**
     * 
     */
    value:boolean;
}