import { IEnumValue } from "./ienumvalue";
import { EnumType } from "./enumtype";

/**
 * Integer
 */
export class EnumIntegerValue implements IEnumValue{
    getEnumType(): EnumType {
        return EnumType.IntType;
    }
    
    getEnumValueName():string{
        return this.name;
    }

    getEnumValue():any{
        return this.value;
    }

    /**
     * 
     */
    name:string;

    /**
     * 
     */
    value:number;
}