export interface RadioIEnumValue{
    getEnumValueName():string;
    getEnumValue():any;

    /**
     * 
     */
    value:string;
}

export class RadioEnumValue implements RadioIEnumValue{

    getEnumValueName():string{
        return this.name;
    }

    getEnumValue():any{
        return this.value;
    }
    
    /**
     * 
     */
    name:string;

    /**
     * 
     */
    value:string;
}