import { FieldConfig } from './fieldconfig/fieldconfig';

export class listFilterItem{
    id: string;
    name: string;
    width: Number; // 
    isExtend: boolean
    constructor(_id: string, _groupName: string) {
        this.id = _id;
        this.name = _groupName;
        this.width = 0;
        this.isExtend = false;
    }
    // 
    setWidth(value: string) {
        this.width = parseInt(value, 10);
    }
    // 
    getWidth(): any {
        return this.width;
    }
}
export class listFilterGroup {
    listItems:Array<listFilterItem>;
    responsedIndex:number = 0;
    constructor(){
        this.listItems = [];
    }
}

