import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class InputGroupControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.InputGroup;
    }
    isRequired(): boolean {
        return !!this.required;
    }

    /**
     * 
     */
    editable: boolean;

    /**
     * 
     */
    groupText: string;

    /**
     * 
     */
    usageMode: string;

    /**
     * 
     */
    dialogOptions: DialogConfig;

    /**
     * 
     */
    click: any;
    /**
     * 
     */
    isExtend:boolean;
    /**label */
    showLabel:boolean;
    /** */
    required:boolean;
    /**
     * 
     */
    placeholder:string;
}

export class DialogConfig {
    modalId: string;
    mapFields: any;
    title: string;
    width: number;
    height: number;
    // showButtons: boolean;
    // buttons: DialogButtonConfig[];
}

export class DialogButtonConfig {
    text: string;
    cls: any;
    handle: any;
}