import { IEnumValue } from "./ienumvalue";
import { EnumType } from "./enumtype";

/**
 * String
 */
export class EnumStringValue implements IEnumValue{
    getEnumType(): EnumType {
        return  EnumType.StringType;
    }

    getEnumValueName():string{
        return this.name;
    }

    getEnumValue():any{
        return this.value;
    }
    
    /**
     * 
     */
    name:string;

    /**
     * 
     */
    value:string;
}