import { Injectable } from '@angular/core';
import { QueryCondition } from '../entity/querycondition';
@Injectable()
export class SolutionService {

    filterEditors = {};

    constructor() { }

    getGuid() {
        return (this.s4() + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + this.s4() + this.s4());
    }

    private s4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }

    /**
     * LableTitle
     * @param field
     */
    getConditionItemTitle(field: QueryCondition) {
        if (field.id && field.id !== '') {
            return field.fieldName;
        }
    }

    /**
     *  JSON.Parse(JSON.Stringify([input])),
     * @param input
     */
    arrayClone(input: Array<any>): Array<any> {
        let result = new Array<any>();
        if (input && input.length > 0) {
            input.forEach(element => {
                result.push(Object.assign({}, element));
            });
        }
        return result;
    }

    addFilterEditorInstance(fieldName: string, cmpIns: any) {
        this.filterEditors[fieldName] = cmpIns;
    }
}
