import { CompareType } from "./comparetype";
import { ValueType } from "./valuetype";
import { RelationType } from "./relationtype";
import { IControl, ControlConvert } from "./controltype/icontrol";
import { IValue, ConditionValueConvert } from "./conditionvalue/ivalue";

/**
 * 
 */
export class QueryCondition{

    /**
     * 
     */
    id: string;

    /**
     * 
     */
    leftBrackets: string;

    /**
     * 
     */
    fieldCode: string;

    /**
     * 
     */
    fieldName: string;
    name: string;

    /**
     * 
     */
    compareType: CompareType;

    /**
     * 
     */
    valueType: ValueType;

    /**
     * 
     * IValue
     * 1. TextValue
     * 2. DropDownListValue
     * 3. DateRangeValue
     * 4. NumberRangeValue
     * 5. SmartHelpValue
     */
    value: IValue;

    /**
     * 
     */
    rightBrackets: string;

    /**
     * 
     */
    relationType: RelationType;

    /**
     * 
     * IControl
     * 1. TextControl
     * 2. DropDownListControl
     * 3. DateRangeControl
     * 4. NumberRangeControl
     * 5. SmartHelpContol
     */
    control: IControl;
    // 
    placeHolder: string;
    beginPlaceHolder?: string;
    endPlaceHolder?: string;
}

